  border-top-width: 0;
  border-bottom-color: #ffffff;
}
.popover.left {
  margin-left: -10px;
}
.popover.left > .arrow {
  top: 50%;
  right: -22px;
  margin-top: -11px;
  border-right-width: 0;
  border-left-color: #e6e6e6;
}
.popover.left > .arrow:after {
  right: 7px;
  bottom: -10px;
  content: " ";
  border-right-width: 0;
  border-left-color: #ffffff;
}
.popover > .arrow {
  border-width: 11px;
}
.popover > .arrow:after {
